import {SubmissionError} from "redux-form";
import {executeQuery} from "./searchReducer";
import {download} from "./utils";
import {saveState} from "./localStorage";

const keyWords = new Set(['feed', 'news', 'music', 'edit', 'settings', 'im', 'friends', 'groups', 'video', 'apps', 'vkpay', 'market', 'docs', 'blog', 'dev', "biz", "about", "jobs", "legal", "data_protection", "safety", "exilemusic", "exilemusic", "verify", "exile_music"]);

export const extractFromString = (values) => {
    return (dispatch, getState) => {
        const text = values.text;
        if (!text) {
            throw new SubmissionError({text: ' '});
        }
        const regex = /href="\/(\w+)"/g;
        let match = regex.exec(text);
        if (!match) {
            throw new SubmissionError({text: '   '});
        }
        let matches = new Set();
        while (match) {
            matches.add(match[1]);
            match = regex.exec(text);
        }
        matches = [...matches.difference(keyWords)];
        const queryString = 'vkUsers (' + matches.join(' ') + ')';
        dispatch(executeQuery(queryString));
    }
};

export const downloadWorkSpace = () => {
    return (dispatch, getState) => {
        const filename = `NTrail workspace (${getState().workSpace.clusters.clusters.length} clusters)`;
        download(JSON.stringify(getState()), filename, '')
    }
};

export const uploadWorkSpace = () => {
    return (dispatch, getState) => {
        const input = document.createElement('input');
        input.type = 'file';
        input.onchange = e => {
            const file = e.target.files[0];

            const reader = new FileReader();
            reader.readAsText(file, 'UTF-8');

            reader.onload = readerEvent => {
                const content = readerEvent.target.result;
                console.log('File loaded');
                try {
                    const state = JSON.parse(content);
                    if (!('form' in state))
                        throw Error('');
                    saveState(state);
                } catch (e) {
                    alert('    NTrail ! (   ,      "  ")    ')
                }
                window.location.reload(true);
            }
        };
        input.click();
    }
};