import React, {useState} from "react";
import {Accordion, Icon, Input, Table} from "semantic-ui-react";
import {connect} from "react-redux";
import {changeActivePropertyId} from "../../../../store/controlsReducer";

const AllProperties = (props) => {
    const toggleProperty = (e, titleProps) => {
        const itemId = titleProps.index;
        const newActiveId = props.activePropertyId === itemId ? -1 : itemId;

        props.changeActivePropertyId(newActiveId);
    };
    const [searchValue, setInputValue] = useState('');
    const searchIcon = (
        <Icon
            name={searchValue.length ? 'close' : 'search'}
            onClick={() => {
                if(searchValue.length)
                    setInputValue('')
            }}
            link
        />
    );
    return (
        <>
            <Input fluid icon={searchIcon} value={searchValue}
                   placeholder={', 12, ,  '}
                   onChange={(e, data) => {
                       setInputValue(data.value)
                   }}
            />
            <Accordion>
                {props.allProperties.map((property) => (
                    <Accordion.Accordion key={property.id}>
                        <Accordion.Title
                            index={property.id}
                            active={props.activePropertyId === property.id}
                            onClick={toggleProperty}
                        >
                            <Icon name="dropdown"/>
                            {props.activePropertyId === property.id ? <b>{property.name}</b> : property.name}

                            <Icon
                                link
                                circular
                                style={{float: "right"}}
                                name="chart line"
                            />
                        </Accordion.Title>
                        <Accordion.Content active={props.activePropertyId === property.id}>
                            <Table selectable>
                                <Table.Body>
                                    {property.values.map(subProperty => (
                                        <Table.Row
                                            key={subProperty.name}>
                                            <Table.Cell width="10">{subProperty.name}</Table.Cell>
                                            <Table.Cell width="5">{subProperty.value}</Table.Cell>
                                            <Table.Cell width="2">
                                                <Icon link name="eye" color="grey"/>
                                            </Table.Cell>
                                        </Table.Row>
                                    ))}
                                </Table.Body>
                            </Table>
                        </Accordion.Content>
                    </Accordion.Accordion>
                ))}
            </Accordion>
        </>
    );
}

const mapStateToProps = (state) => ({
    allProperties: state.workSpace.controls.properties.all,
    activePropertyId: state.workSpace.controls.activePropertyId
});

export default connect(mapStateToProps, {changeActivePropertyId})(AllProperties)
